.services {
  padding: 8rem 0;
  background: #fafafa;
  width: 100%;
  height: 100%;
  & h2 {
    color: black;
    font-size: 2rem;
    text-align: center;
    margin-bottom: 3rem;
  }
  &__search{
    display: flex;
    flex-direction: column;
    position: relative;
  }
  & input{
    border-color: black;
    height: 2rem;
    padding: 1.2rem;
    width: 13rem;
    outline: none;
    border-radius: 1rem;
    position: absolute;
    top: 4rem;
    left: 50%;
    transform: translateX(-50%);
  }
  &__notfound{
    text-align: center;
    color: black;
    font-size: 1.5rem;
    position: absolute;
    top: 25rem;
    left: 50%;
    transform: translateX(-50%);
    & a{
      color: darkblue;
      transition: var(--transition);
      &:hover{
        color: #a6ff00;
        font-size: 2rem;
        transition: var(--transition);
      }
    }
  }
}

table {
  width: 100%;
  height: 30rem;
  border-collapse: collapse;
  border-color: black;
  margin: 3rem auto;
}

th {
  border-bottom: 1px solid black;
  color: black;
  padding: 0.5rem;
  border: 2px solid black;
}

td {
  text-align: center;
  border: 2px solid black;
  color: black;
  padding: 0.5rem;
  &:hover{
    color:#a6ff00;
    background: rgba(0, 0, 0, 0.9);
  }
  & a{
    color: black;
    &:hover{
      color: #a6ff00;
    }
  }
}



@media screen and (max-width: 1024px) {
  .services{
    &__notfound{
      font-size: 0.7rem;
      top: 24rem;
    }
    & h2{
      font-size: 1.5rem;
    }
  }
  th {
    padding: 0.2rem;
  }
  td {
    padding: 0.5rem;
    font-size: 1.2rem;
  }
  table{
    width: 75%;
    height: 10rem;
  }
}


@media screen and (max-width: 600px) {
  .services{
    & input{
      top: 4rem;
    }
    &__notfound{
      font-size: 0.5rem;
      margin-bottom: 4rem;
      top: 20rem;
    }
  }
  table{
    width: 50%;
  }
  th {
    padding: 0.3rem;
    font-size: 0.8rem;
  }
  td {
    padding: 0.3rem;
    font-size: 0.6rem;
  }
}